import React from 'react'
import PropTypes from 'prop-types'
// import { Link } from 'react-router-dom'


export default function Navbar(props) {
  return (
    <nav className={`navbar navbar-expand-lg navbar-${props.mode} bg-${props.mode}`}>
      <div className="container-fluid">
        <a className="navbar-brand" href="#">{props.title}</a>
        <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav me-auto mb-2 mb-lg-0">
            <li className="nav-item">
              <a className="nav-link active" aria-current="page" href="#">Home</a>
            </li>
            {/* <li className="nav-item">
              <Link className="nav-link active" aria-current="page" to="/about">{props.About}</Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link active" aria-current="page" to="/contact">Contact</Link>
            </li> */}
          </ul>
          
          <div className={`form-check mode-1 ${props.yellowWhite === 'light' ? '$teal-300' : 'light'}`}>
            <input className="form-check-input" onClick={props.yellowMode} type="radio" name="exampleRadios" id="exampleRadios2" value="option2" />
            <label className="form-check-label" htmlFor="exampleRadios2"></label>
          </div>
          
          <div className={`form-check mode-2 ${props.cyanwhite === 'light' ? '$cyan-500' : 'light'}`}>
            <input className="form-check-input" onClick={props.cyanMode} type="radio" name="exampleRadios" id="exampleRadios2" value="option2" />
            <label className="form-check-label" htmlFor="exampleRadios2"></label>
          </div>

          <div className={`form-check mode-3 ${props.white === 'light' ? '$pink-300' : 'light'}`}>
            <input className="form-check-input" onClick={props.pinkMode} type="radio" name="exampleRadios" id="exampleRadios3" value="option3" />
            <label className="form-check-label" htmlFor="exampleRadios3"></label>
          </div>
          
          <div className={`form-check mode-4 ${props.mode === 'light' ? 'dark' : 'light'}`}>
            <input className="form-check-input" onClick={props.toggleMode} type="radio" name="exampleRadios" id="exampleRadios3" value="option3" />
            <label className="form-check-label" htmlFor="exampleRadios3"><i>Click again to <strong>reset</strong></i>    </label>
          </div>

          <form className="d-flex mx-4 mode-4" role="search">
            <input className="form-control me-2 " type="search" placeholder="Search" aria-label="Search" />
            <button className="btn btn-warning" type="submit">Search</button>
          </form>
        </div>
      </div>
    </nav>

  )
}

Navbar.propTypes = {
  title: PropTypes.string,
  About: PropTypes.string
}

