import './App.css';
import React from 'react';
// import About from './components/About';
// import contactus from './components/contactus';
import Navbar from './components/Navbar';
import Form from './components/Form';
import { useState } from 'react';
import Alert from './components/Alert';
// import {
//   Routes,
//   Route,
//   BrowserRouter,
// } from "react-router-dom";

function App() {
  const [mode, setmode] = useState('light');
  const [white, setpink] = useState('light');
  const [alert, setAlert] = useState(null);
  const [cyanWhite, setCyan] = useState("light");
  const [yellowWhite, setYellow] = useState("light");

  const showAlert = (type, message) => {
    setAlert({
      msg: message,
      Type: type
    })
    setTimeout(() => {
      setAlert(null)
    }, 800);
  }
  const toggleMode = () => {
    if (mode === "light") {
      setmode('dark');
      document.body.style.backgroundColor = 'black'
      showAlert("success ", "Dark mode has been enabled")
    }
    else {
      setmode('light');
      document.body.style.backgroundColor = 'white'
      showAlert("success ", "Light mode has been enabled")
    }

  }
  const pinkMode = () => {
    if (white === "light") {
      setpink('$pink-300');
      document.body.style.backgroundColor = '#e685b5'
      document.body.style.color = 'white'
      showAlert("success ", "Pink Mode has been enabled")
    }
    else {
      setpink('light');
      document.body.style.backgroundColor = 'white'
      showAlert("success ", "Light mode has been enabled")
    }

  }
  const cyanMode = () => {
    if (cyanWhite === "light") {
      setCyan('$cyan-500');
      document.body.style.backgroundColor = '#6edff6'
      showAlert("success ", "Cyan Mode has been enabled")
    }
    else {
      setmode('light');
      document.body.style.backgroundColor = 'white'
      showAlert("success ", "Light mode has been enabled")
    }

  }
  const yellowMode = () => {
    if (yellowWhite === "light") {
      setYellow('$teal-300');
      document.body.style.backgroundColor = '#ffcd39'
      showAlert("success ", "Yellow Mode has been enabled")
    }
    else {
      setYellow('light');
      document.body.style.backgroundColor = 'white'
      showAlert("success ", "Light mode has been enabled")
    }

  }
  return (
    <>
      {/* <BrowserRouter> */}
        <Navbar title="TextEasy" About="About" mode={mode} toggleMode={toggleMode} pinkMode={pinkMode} yellowMode={yellowMode} cyanMode={cyanMode} />
        <Alert alert={alert} />
        <div className="container  my-5" >
        <Form heading="Enter Your Text Here" mode={mode} showAlert={showAlert}/>
        {/* <Routes> */}
        {/* <Route exact path="/about" element={<About/>}/>  */}
            {/* <Route exact path="/" element={<Form heading="Enter Your Text Here" mode={mode} showAlert={showAlert}/>}/> */}
        {/* <Route exact path="/contact" element={<contactus/>}/>  */}
        {/* </Routes> */}
        </div>
        {/* </BrowserRouter> */}
    </>
  )
}


export default App;

