import { useState } from "react"
import React from 'react'


export default function Form(props) {
    const UpperCase = () => {
        let newtext = text.toUpperCase();
        setText(newtext);
        props.showAlert("success", "Text is set to UPPERCASE")
    }
    const lowerCase = () => {
        let newtext = text.toLowerCase();
        setText(newtext);
        props.showAlert("success", "Text is set to lowercase")
    }

    const [text, setText] = useState("");

    const Clear = () => {
        let newtext = ("");
        setText(newtext);
        props.showAlert("success", "Cleared Your Text")
    }

    const Copy = () => {
        let text = document.getElementById("box");
        text.select();
        navigator.clipboard.writeText(text.value);
        props.showAlert("success", "Copied to Clipboard!!")
    }


    const handleOnChange = (event) => {
        setText(event.target.value);
    }
    return (
        <>
            <div className={`mb-3 text-${props.mode === 'light' ? 'dark' : 'light'} ${props.white === 'light' ? 'pink-300' : 'light'} ${props.yellowWhite === 'light' ? '$yellow-300' : 'light'} ${props.cyanWhite === 'light' ? '#6edff6' : 'light'}`}>
                <h1>{props.heading}</h1>
                <textarea className={`form-control ${props.white === 'light' ? 'pink-300' : 'light'}`} id="box" rows="6" placeholder="Enter Your Text" value={text} onChange={handleOnChange}></textarea>
                <button className='btn btn-primary mx-4 my-3' onClick={UpperCase}>Convert to UPPERCASE</button>
                <button className='btn btn-primary' onClick={lowerCase}>Convert to lowercase</button>
                <button className='btn btn-primary mx-4' onClick={Clear}>Clear</button>
                <button className='btn btn-primary'>Enter Clicked {text.split(/\n/).length - 1} times.</button>
                <button className='btn btn-primary mx-4' onClick={Copy}>Copy to Clipboard</button>
            </div>
            <div className={`container text-${props.mode === 'light' ? 'dark' : 'light'} ${props.white === 'light' ? '$pink-300' : 'light'} ${props.greenwhite === 'light' ? '$teal-300' : 'light'} ${props.cyanwhite === 'light' ? '$cyan-500' : 'light'}`}>
                <h1>More Information</h1>
                <p>Your Text has <b>{text.split(' ').filter(function (n) { return n !== '' }).length}</b> Words as well as <b>{text.replace(/ /g," ").length}</b> Characters.</p>
                <br />
                <p>An Intermediate Reader can Silently Read this in <b>{0.21 * text.split(" ").filter(function (n) { return n !== '' }).length} Seconds.</b></p>
                <p>A Pro Reader can Silently Read this in <b>{0.15 * text.split(" ").filter(function (n) { return n !== '' }).length} Seconds.</b></p>
                <h2 className="my-3">Preview Of Your Text</h2>
                <p>{text.length > 0 ? text : 'Enter text in the textarea to Preview it'}</p>

            </div>
        </>
    )
}
